@import '../../variables';
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    padding: 10px 0px;
    background-color: #272B35;
    font-weight: bold;
    z-index: 99999;
    @include flex-center(_,space-between,center);
    &__nav {
        @include flex-center(row,flex-end,center); }
    &__logo {
        margin: 0px 10px;
        width: 100px; }

    &__list {
        margin: 0px 10px;
        @include flex-center(_,center,center);
        &-ul {
            @include flex-center(_,center,center); }
        &-li {
            position: relative;
            top: 10px;
            cursor: pointer;
            list-style-type: none;
            margin: 10px;
            padding-bottom: 5px;
            border-bottom: solid 0px white;
            transition: border-width 0.3s linear;
            color: #EBEBEB;
            &:hover {
                border-width: 5px;
                color: silver;
                border-color: silver; } } }
    &__burguer {
        font-size: 25px;
        padding: 5px;
        margin: 10px;
        border-radius: 5px;
        color: white;
        &--press {
            color:#2A80B9 {} }
        &:hover {
            color: $color-secondary-strong;
            transition-duration: .400s;
            border-color: $color-secondary-strong; } }
    &__select {
        margin: 0px 5px;
        border: none;
        padding: 5px;
        crusor: pointer;
        font-size: 20px;
        color: white;
        background-color: transparent;
        &:focus {
            outline: none; } }

    &__dropdown {
        display: none;
        &-ul {
            transition-duration: .500s;
            position: absolute;
            top: 70px;
            right: 0px;
            @include flex-center(column,center,flex-start); }
        &-row {
            background-color: #272C35;
            color: white;
            width: 100%;
            padding: 10px 40px;
            margin: 0;
            cursor: pointer;
            list-style-type: none;
            border: 1px solid white;
            @include flex-center(row,space-between,center);
            &:hover {
                color: #2A80B9;
                opacity: 0.9;
                transition-duration: .400s; } }
        &-li {
            width: 100%; } } }



@media screen and (max-width: 800px) {
    .navbar__list {
        display: none; }
    .navbar__dropdown {
        display: block; } }
